import React, {useEffect, useRef} from "react";
import {
  Container,
  Row,
  Col,
} from "reactstrap";
import {useSelector} from 'react-redux';
import PotentialCandidates from 'components/candidates/PotentialCandidates';
import FeaturedRecordings from 'components/recordings/FeaturedRecordings';
import NotificationSystem from 'react-notification-system';

function Bookmarks() {
  const notificationSystem  = useRef(null);
  const zoomUser = useSelector(state => state.zoomData.zoomUser);

  useEffect(() => {
    if (!zoomUser) {
      const notification = notificationSystem.current;
      notification.addNotification({
        title: 'Alert',
        message: 'Please login with zoom!',
        level: 'info',
      });
    }
  }, [])

  return (
    <Container className="mt--0" fluid>
      <h1 className="main-page-title">Bookmarks</h1>
      <Row>
        <Col xl="12">
          <Row>
            <div className="col">
              <FeaturedRecordings />
            </div>
          </Row>
        </Col>
      </Row>

      <Row>
        <Col xl="12">
          <Row>
            <div className="col">

              <PotentialCandidates />

            </div>
          </Row>
        </Col>
      </Row>
      <NotificationSystem ref={notificationSystem} />
    </Container>
  )
}

export default Bookmarks;