import React, { useState, useEffect, useRef } from 'react';
import {
    Container,
    Row,
    Col,
    Button,
} from "reactstrap";
import { loadStripe } from "@stripe/stripe-js";
import { stripeConfig } from 'config';
import { apiUrl } from '../../../config';
import { useSelector, useDispatch } from 'react-redux';
import { paymentSuccess, clearUserError } from 'store/users/actions';
import NotificationSystem from 'react-notification-system';

const stripePromise = loadStripe(stripeConfig.STRIPE_PUBLISHABLE_KEY);

export default function Payment() {
    const [message, setMessage] = useState("");
    const dispatch = useDispatch();
    const notificationSystem = useRef(null);
    const user = useSelector(state => state.users.user);
    const token = useSelector(state => state.users.token);
    const userError = useSelector(state => state.users.userError);

    useEffect(() => {
        // Check to see if this is a redirect back from Checkout
        const query = new URLSearchParams(window.location.search);

        if (query.get("success")) {
            setMessage("success");
            dispatch(paymentSuccess(token, user.id));
        }

        if (query.get("canceled")) {
            setMessage("canceld");
        }
    }, []);

    useEffect(() => {
        if (userError !== '') {
            const notification = notificationSystem.current;
            notification.addNotification({
                title: 'Payment Status Update failed!',
                message: userError,
                level: 'error',
            });

            dispatch(clearUserError());
        }
    }, [userError]);

    useEffect(() => {
        if (message !== '') {
            const notification = notificationSystem.current;

            if (message === 'success') {
                notification.addNotification({
                    title: 'Payment Success!',
                    message: 'Service fee paid! You will receive an email confirmation.',
                    level: 'success',
                });
            } else if (message === 'canceled') {
                notification.addNotification({
                    title: 'Payment Canceld!',
                    message: 'Payment canceled -- continue to shop around and checkout when you\'re ready.',
                    level: 'error',
                });
            }
        }
    }, [message]);

    const handleClick = async (event) => {
        const stripe = await stripePromise;

        const response = await fetch(apiUrl.MAKE_PAYMENT, {
            method: "POST",
        });

        const session = await response.json();

        // When the customer clicks on the button, redirect them to Checkout.
        const result = await stripe.redirectToCheckout({
            sessionId: session.id,
        });

        if (result.error) {
            // If `redirectToCheckout` fails due to a browser or network
            // error, display the localized error message to your customer
            // using `result.error.message`.
        }
    };

    return (
        <>
            <Container className="mt-0" fluid>
                {
                    user && user.lastMonthUsageAmount > 0 && !user.lastMonthPaidStatus &&
                    <>
                        <h4 className="h2 BigTitle text-center mb-4">Your Service Usage for last month</h4>
                        <h5 className="text-center h3">Total Recordings: 350min</h5>
                        <h5 className="text-center h3 mb-4">Total Price: $350</h5>

                        <Row>
                            <Col className="text-center">
                                <Button type="button" className="btn-success" id="checkout-button" role="link" onClick={handleClick}>Make a Payment</Button>
                            </Col>
                        </Row>
                    </>
                }
                {
                    user && user.lastMonthUsageAmount > 0 && user.lastMonthPaidStatus &&
                    <h4 className="h3 text-center">Thank you.  You paid for last month</h4>
                }
                {
                    user && user.lastMonthUsageAmount === 0 &&
                    <h4 className="h3 text-center">You didn't use our service last month</h4>
                }
            </Container>
            <NotificationSystem ref={notificationSystem} />

        </>
    )
}