import React, { useState, useEffect } from 'react';
import moment from 'moment';
import classNames from 'classnames';
import { useSelector, useDispatch } from 'react-redux';
import { changeCandidateBookmark } from 'store/zoom/actions';

export default function Candidate({ candidate }) {
    const dispatch = useDispatch();
    const bookmarkClass = classNames('fas fa-bookmark', candidate.bookmarked ? 'text-info' : 'text-light');

    const token = useSelector(state => state.users.token);

    const handleBookmark = () => {
        dispatch(changeCandidateBookmark(token, candidate.email, !candidate.bookmarked));
    }

    return (
        <tr>
            <td className="name">
                {candidate.name}
            </td>
            <td className="email">
                {candidate.email}
            </td>
            <td className="meeting_name">
                {candidate.topic}
            </td>
            <td className="last_active">
                {moment(candidate.updatedAt).format('h:mm A, MMM D YYYY')}
            </td>
            <td className="text-right">
                <i className={bookmarkClass} onClick={handleBookmark}></i>
            </td>
        </tr>
    )
}