import * as types from './types'

const initialState = {
  user: null,
  signinError: '',
  token: null,
  userError: '',
  message: ''
}

const user = (state = initialState, action) => {
  switch (action.type) {
    //REGISTER
    case types.REGISTER_SUCCESS:
      return { ...state, user: action.data.user, token: action.data.token }

    //SIGNIN
    case types.SIGNIN_SUCCESS:
      return { ...state, user: action.data.user, token: action.data.token }

    case types.SIGNIN_ERROR:
      return { ...state, signinError: action.error }

    case types.CLEAR_SIGNIN_ERROR:
      return { ...state, signinError: '' }

    //UPDATE PROFILE
    case types.UPDATE_PROFILE_SUCCESS:
      return { ...state, user: { ...state.user, ...action.user } }

    case types.UPDATE_PROFILE_ERROR:
      return { ...state, userError: action.error }

    case types.CLEAR_USER_ERROR:
      return { ...state, userError: '' }

    case types.USER_MESSAGE:
      return { ...state, message: action.message }

    case types.CLEAR_USER_MESSAGE:
      return { ...state, message: '' }

    // UPDATE PAYMENT STATUS
    case types.UPDATE_PAYMENT_STATUS_SUCCESS:
      return { ...state, user: { ...state.user, lastMonthPaidStatus: action.lastMonthPaidStatus } }

    case types.UPDATE_PAYMENT_STATUS_FAILED:
      return { ...state, userError: action.error }

    //SIGNOUT
    case types.SIGNOUT_SUCCESS:
      return initialState

    default:
      return state
  }
}

export default user
