const env = process.env.NODE_ENV;
let apiOriginUrl = '';

switch (env) {
  case 'production':
    apiOriginUrl = 'https://audaxbackend.ukcourier.a2hosted.com';
    break;
  case 'testing':
    apiOriginUrl = 'http://localhost:4000';
    break;
  default:
    apiOriginUrl = 'http://localhost:4000';
    break;
}

const apiUrl = {
  // Register
  REGISTER: `${apiOriginUrl}/api/auth/signup`,

  //Session
  SIGNIN: `${apiOriginUrl}/api/auth/signin`,
  SIGNOUT: `${apiOriginUrl}/users/logout`,
  UPDATE_PROFILE: `${apiOriginUrl}/api/user/update`,
  UPDATE_PAYMENT_STATUS: `${apiOriginUrl}/api/user/update_payment_status`,

  // zoom connect
  GET_ACCESS_TOKEN: `${apiOriginUrl}/api/zoom/get_token`,
  REFRESH_ZOOM_ACCESS_TOKEN: `${apiOriginUrl}/api/zoom/refresh_zoom_access_token`,
  ZOOM_SIGNIN: `${apiOriginUrl}/api/zoom/login`,
  GET_ZOOM_USERINFO: `${apiOriginUrl}/api/zoom/get_zoom_user`,
  GET_ZOOM_USER_MEETINGS: `${apiOriginUrl}/api/zoom/meetings`,
  GET_MEETING: `${apiOriginUrl}/api/zoom/meeting`,
  GET_PARTICIPANTS: `${apiOriginUrl}/api/zoom/get_participants`,
  UPLOAD_RECORDING: `${apiOriginUrl}/api/zoom/uploads3`,
  GET_TOTAL_RECORDING_DATA: `${apiOriginUrl}/api/zoom/get_total_recording_data`,
  GET_TOTAL_CANDATES_COUNT: `${apiOriginUrl}/api/zoom/get_total_candidates_count`,
  GET_MONTHLY_RECORDING_DATA: `${apiOriginUrl}/api/zoom/get_monthly_recording_data`,
  GET_CANDIDATES: `${apiOriginUrl}/api/zoom/get_candidates`,
  CHANGE_CANDIDATE_BOOKMARK: `${apiOriginUrl}/api/zoom/change_candidate_bookmark`,
  GET_RECORDINGS: `${apiOriginUrl}/api/zoom/get_recordings`,
  CHANGE_RECORDING_BOOKMARK: `${apiOriginUrl}/api/zoom/change_recording_bookmark`,

  // aws connect
  GET_TRANSCRIPTS: `${apiOriginUrl}/api/aws/get_transcripts`,
  MAKE_PAYMENT: `${apiOriginUrl}/api/aws/payment`,
  UPDATE_SPEAKERS: `${apiOriginUrl}/api/aws/update_speakers`,
  GET_SPEAKERS: `${apiOriginUrl}/api/aws/get_speakers`,


  SAVE_TRANSCRIPT: `${apiOriginUrl}/api/aws/save_transcript`,
  UPDATE_TRANSCRIPT: `${apiOriginUrl}/api/aws/update_transcript`,
  ADD_TRANSCRIPT_COMMENT: `${apiOriginUrl}/api/aws/add_transcript_comment`,
};

export default apiUrl;
