import React, { useState } from 'react';
import moment from 'moment';

export default function Comment({ comment, removeComment, editComment }) {
    const [hiddenActions, setHiddenActions] = useState(true);

    const editHandler = () => {
        editComment(comment.id, comment.commentOf);
        setHiddenActions(true);
    }

    return (
        <div className="custom-card highlight-card">
            <div className="custom-card-body">
                <i className="fas fa-ellipsis-v" onClick={() => setHiddenActions(!hiddenActions)}></i>
                <span>{comment.commentUser}</span>
                <p className="comment-text">
                    <a href={`#${comment.id}`}>
                        {comment.commentText}
                    </a>
                </p>
            </div>
            <span className="custom-card-footer">{`Commented - ${moment(comment.commentTime).format('h:mm A MMM D')}`}</span>
            <div className="action-buttons" hidden={hiddenActions}>
                <p>
                    <button type="button" onClick={editHandler}>Edit</button>
                </p>
                <p>
                    <button type="button" onClick={() => removeComment(comment.id, comment.commentOf)}>Delete</button>
                </p>
            </div>
        </div>
    )
}