import * as types from './types'
import { apiUrl } from 'config'

export const getTranscripts = (token, userId, meetingUUID) => {
  return (dispatch) => {
    fetch(apiUrl.GET_TRANSCRIPTS, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json',
        'Authorization': 'Bearer ' + token
      },
      body: JSON.stringify({
        userId,
        meetingUUID,
      })
    })
      .then(response => response.json())
      .then(data => {
        if (!data.error) {
          dispatch(getTranscriptsSuccess(data.scriptData));
          if (data.first) {
            dispatch(saveTranscripts(token, userId, meetingUUID, data.scriptData));
          }
        } else {
          console.log(data.errorObj)
        }
      })
      .catch(error => {
        console.log(error)
      })
  }
}

export const saveTranscripts = (token, userId, meetingUUID, transcript) => {
  return dispatch => {
    fetch(apiUrl.SAVE_TRANSCRIPT, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json',
        'Authorization': 'Bearer ' + token
      },
      body: JSON.stringify({
        userId,
        meetingUUID,
        transcript
      })
    })
      .then(response => response.json())
      .then(data => {
        if (!data.error) {
          dispatch(getTranscriptsSuccess(data.scriptData));
        }
      })
      .catch(error => {
        console.log(error)
      })
  }
}

