import * as types from './types'
import { apiUrl } from '../../config'

export const getAccessToken = (token, code) => {
  return (dispatch) => {
    fetch(apiUrl.GET_ACCESS_TOKEN, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json',
        'Authorization': "Bearer " + token,
      },
      body: JSON.stringify({
        code: code
      })
    })
      .then(response => response.json())
      .then(data => {
        console.log(data)
        if (!data.error) {
          dispatch(getAccessTokenSuccess(data.zoomData));
          dispatch(refreshZoomToken(token, data.zoomData.zoomRefreshToken));
        } else {
          dispatch(getAccessTokenError(data.errorObj))
        }
      })
      .catch(error => {
        // dispatch(getAccessTokenError(error))
        console.log(error)
      })
  }
}

