export const refreshZoomToken = (token, rtoken) => {

  return dispatch => {
    console.log("start")
    setTimeout(() => {
      console.log("get refresh token")
      fetch(apiUrl.REFRESH_ZOOM_ACCESS_TOKEN, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json',
          'Authorization': "Bearer " + token,
        },
        body: JSON.stringify({
          rtoken
        })
      })
        .then(response => response.json())
        .then(data => {
          if (!data.error) {
            console.log(data)
            dispatch(refreshZoomTokenSuccess(data.tokens))
          } else {
            console.log(data)
          }
        })
        .catch(error => {
          console.log(error)
        })
    }, 3500000);
  }
}

const refreshZoomTokenSuccess = data => ({
  type: types.REFRESH_ZOOM_ACCESS_TOKEN_SUCCESS,
  data
})

