import React, { useState, useRef } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import NotificationSystem from 'react-notification-system';
import { updateTranscript } from 'store/aws/actions';
// import './CommentBox.css';

function CommentBox(props) {
  const [comment, setComment] = useState('');
  const dispatch = useDispatch();
  const notificationSystem = useRef(null);
  const token = useSelector(state => state.users.token);
  const zoomUser = useSelector(state => state.zoomData.zoomUser);

  const handleCommentSubmit = (e) => {
    e.preventDefault();

    const {
      userId,
      meetingUUID,
      transcripts,
      selectedRange,
      toggleCommentBox,
      toggleButtonsGroup
    } = props;

    if (selectedRange.startContainer.parentNode.nodeName !== 'P' || selectedRange.startContainer.parentNode.nodeName !== 'P' || selectedRange.startContainer.textContent !== selectedRange.endContainer.textContent) {
      const notification = notificationSystem.current;

      notification.addNotification({
        title: 'Selection Error',
        message: 'Unable to comment, please reselect text!',
        level: 'error',
      });

      toggleButtonsGroup();
      toggleCommentBox();
      reset()
      return
    }

    if (comment) {
      const uniqueId = Date.now();
      const commentTime = new Date();

      wrapSelectedTextWithId(uniqueId);

      const p = selectedRange.commonAncestorContainer;
      const html = p.innerHTML
      const pIndex = p.dataset.index;

      let { dialog } = transcripts;

      let updatedDialog = JSON.parse(JSON.stringify(dialog));

      updatedDialog[pIndex].line = html;

      const commentObj = {
        id: uniqueId,
        commentText: comment,
        commentTime,
        commentOf: pIndex,
        commentUser: zoomUser ? `${zoomUser.first_name} ${zoomUser.last_name}` : ''
      }

      let updatedTranscript = { ...transcripts, dialog: updatedDialog, comments: [...transcripts.comments, commentObj] }

      dispatch(updateTranscript(token, userId, meetingUUID, updatedTranscript));

      toggleButtonsGroup();
      toggleCommentBox();
      reset();
    }
  }

  const handleCommentChange = (e) => {
    setComment(e.target.value);
  }

  const reset = () => { setComment(''); }

  const wrapSelectedTextWithId = (uniqueId) => {
    const markWrapper = document.createElement('mark');
    markWrapper.setAttribute('id', uniqueId);
    props.selectedRange.surroundContents(markWrapper);
  }

  return (
    <>
      <div hidden={props.hidden} className="comment-box-wrapper custom-card">
        <form
          className="comment-box"
          onSubmit={handleCommentSubmit}
        >
          <label htmlFor="commentBox" className="visuallyhidden">
            Add your comment
          </label>
          <textarea
            id="commentBox"
            className="comment-box__text-area"
            placeholder="Add your comment"
            onChange={handleCommentChange}
            value={comment}
            rows="6"
          >
          </textarea>
          <button type="submit" className="btn btn-outline-primary btn-block">
            submit
          </button>
        </form>
      </div>
      <NotificationSystem ref={notificationSystem} />
    </>
  );
}

export default CommentBox;
