import React, { useState, useEffect, useRef } from "react";
import {
  Button,
  Card,
  CardHeader,
  CardBody,
  CardImg,
  FormGroup,
  Form,
  Input,
  Container,
  Row,
  Col,
} from "reactstrap";
import { useDispatch, useSelector } from 'react-redux';
import NotificationSystem from 'react-notification-system';
import { updateProfile, clearUserMessage, clearUserError, signout } from 'store/users/actions';
import Payment from 'views/pages/stripe/Payment';

function Profile() {
  const [state, setSate] = useState({
    editable: false,
    id: 0,
    userName: '',
    email: '',
    firstName: '',
    lastName: '',
    address: '',
    city: '',
    country: '',
    postalcode: '',
    aboutMe: ''
  });

  const dispatch = useDispatch();
  const notificationSystem = useRef(null);
  const user = useSelector(state => state.users.user);
  const token = useSelector(state => state.users.token);
  const message = useSelector(state => state.users.message);
  const userError = useSelector(state => state.users.userError);
  const zoomUser = useSelector(state => state.zoomData.zoomUser);

  useEffect(() => {
    setSate({
      ...state,
      id: user.id,
      userName: user.username,
      email: user.email,
      firstName: user.first_name,
      lastName: user.last_name,
      address: user.address,
      city: user.city,
      postalcode: user.postalcode,
      country: user.country,
      aboutMe: user.about_me
    })

    if (!zoomUser) {
      const notification = notificationSystem.current;
      notification.addNotification({
        title: 'Alert',
        message: 'Please login with zoom',
        level: 'info',
      });
    }
  }, []);

  useEffect(() => {
    if (message !== '') {
      const notification = notificationSystem.current;
      notification.addNotification({
        title: 'Update success',
        message: message,
        level: 'success',
      });

      dispatch(clearUserMessage());
    }
  }, [message])

  useEffect(() => {
    if (userError !== '') {
      const notification = notificationSystem.current;
      notification.addNotification({
        title: 'Update failed!',
        message: userError,
        level: 'error',
      });

      dispatch(clearUserError());
    }
  }, [userError])


  const editProfile = () => {
    setSate({ ...state, editable: !state.editable });
  }

  const saveProfile = () => {
    const { id, userName, firstName, lastName, email, address, city, country, postalcode, aboutMe } = state;

    if (!userName) {
      const notification = notificationSystem.current;

      notification.addNotification({
        title: 'Form Error',
        message: 'Name is required!',
        level: 'error',
      });
      return;
    }

    const user = {
      id,
      address,
      city,
      country,
      postalcode,
      username: userName,
      first_name: firstName,
      last_name: lastName,
      about_me: aboutMe
    }

    dispatch(updateProfile(token, user));
  }

  const handleSignOut = () => {
    dispatch(signout())
  }

  return (
    <>
      <Container className="mt-0 profile-header-title" fluid>
        <h1 className="main-page-title">Profile</h1>
      </Container>
      <Container className="mt-6 profile-container" fluid>
        <Row>
          <Col className="order-xl-2" xl="4">
            <Card className="card-profile">
              <CardImg
                alt="..."
                src={require("assets/img/theme/img-1-1000x600.jpg")}
                top
              />
              <Row className="justify-content-center">
                <Col className="order-lg-2" lg="3">
                  <div className="card-profile-image">
                    <a href="#pablo" onClick={(e) => e.preventDefault()}>
                      <img
                        alt="..."
                        className="rounded-circle"
                        src={require("assets/img/avatar2.jpg")}
                      />
                    </a>
                  </div>
                </Col>
              </Row>

              <CardBody className="pt-6 mt-4">
                <div className="text-center">
                  <h5 className="h2">
                    {user.first_name} {user.last_name}
                  </h5>
                  <h5 className="h2">
                    {user.email}
                  </h5>
                  
                </div>
              </CardBody>
            </Card>

            <Card>
              <CardHeader>
                <h5 className="h3 mb-0">Payment</h5>
              </CardHeader>

              <CardBody>
                <Payment />
              </CardBody>
            </Card>

            <Card>
              <CardBody>
                <Button className="btn-primary text-white w-100" onClick={handleSignOut}>
                  Logout Service
                </Button>
              </CardBody>
            </Card>
          </Col>
          <Col className="order-xl-1" xl="8">
            <Row>

            </Row>
            <Card>
              <CardHeader>
                <Row className="align-items-center">
                  <Col xs="6">
                    <h3 className="mb-0">My profile</h3>
                  </Col>
                  <Col xs="6" className="text-right">
                    <Button
                      className="btn-primary my-3"
                      color="default"
                      href="#pablo"
                      onClick={editProfile}
                    >
                      Edit profile
                    </Button>
                  </Col>
                </Row>
              </CardHeader>
              <CardBody>
                <Form>
                  <h6 className="heading-small text-muted mb-4">
                    User information
                  </h6>
                  <div className="pl-lg-4">
                    <Row>
                      <Col lg="6">
                        <FormGroup>
                          <label
                            className="form-control-label"
                            htmlFor="input-username"
                          >
                            Username
                          </label>
                          <Input
                            id="input-username"
                            placeholder="Username"
                            type="text"
                            disabled={!state.editable}
                            value={state.userName}
                            onChange={e => setSate({ ...state, userName: e.target.value })}
                          />
                        </FormGroup>
                      </Col>
                      <Col lg="6">
                        <FormGroup>
                          <label
                            className="form-control-label"
                            htmlFor="input-email"
                          >
                            Email address
                          </label>
                          <Input
                            id="input-email"
                            placeholder="jesse@example.com"
                            type="email"
                            disabled
                            value={state.email}
                          />
                        </FormGroup>
                      </Col>
                    </Row>
                    <Row>
                      <Col lg="6">
                        <FormGroup>
                          <label
                            className="form-control-label"
                            htmlFor="input-first-name"
                          >
                            First name
                          </label>
                          <Input
                            id="input-first-name"
                            placeholder="First name"
                            type="text"
                            disabled={!state.editable}
                            value={state.firstName}
                            onChange={e => setSate({ ...state, firstName: e.target.value })}
                          />
                        </FormGroup>
                      </Col>
                      <Col lg="6">
                        <FormGroup>
                          <label
                            className="form-control-label"
                            htmlFor="input-last-name"
                          >
                            Last name
                          </label>
                          <Input
                            id="input-last-name"
                            placeholder="Last name"
                            type="text"
                            disabled={!state.editable}
                            value={state.lastName}
                            onChange={e => setSate({ ...state, lastName: e.target.value })}
                          />
                        </FormGroup>
                      </Col>
                    </Row>
                  </div>
                  <hr className="my-4" />
                  {
                    zoomUser &&
                    <>
                      <h6 className="heading-small text-muted mb-4">
                        Zoom account information
                      </h6>
                      <Row>
                        <Col lg="6">
                          <FormGroup>
                            <label
                              className="form-control-label"
                              htmlFor="zoom-email"
                            >
                              Email
                            </label>
                            <Input
                              id="zoom-email"
                              placeholder="Email"
                              type="email"
                              disabled={true}
                              value={zoomUser.email}
                            />
                          </FormGroup>
                        </Col>
                        <Col lg="6">
                          <FormGroup>
                            <label
                              className="form-control-label"
                              htmlFor="zoom-firstname"
                            >
                              First Name
                            </label>
                            <Input
                              id="zoom-firstname"
                              placeholder="First Name"
                              type="text"
                              disabled={true}
                              value={zoomUser.first_name}
                            />
                          </FormGroup>
                        </Col>
                        <Col lg="6">
                          <FormGroup>
                            <label
                              className="form-control-label"
                              htmlFor="zoom-lastname"
                            >
                              Last Name
                            </label>
                            <Input
                              id="zoom-lastname"
                              placeholder="Last Name"
                              type="text"
                              disabled={true}
                              value={zoomUser.lastname}
                            />
                          </FormGroup>
                        </Col>
                        <Col lg="6">
                          <FormGroup>
                            <label
                              className="form-control-label"
                              htmlFor="zoom-phonenumber"
                            >
                              Phone Number
                            </label>
                            <Input
                              id="zoom-phonenumber"
                              placeholder="Phone Number"
                              type="text"
                              disabled={true}
                              value={zoomUser.phone_number}
                            />
                          </FormGroup>
                        </Col>
                      </Row>
                      <hr className="my-4" />
                    </>
                  }
                  
                  <div className="pl-lg-4">
                    <FormGroup>
                      <Button
                        type="button"
                        className="btn btn-success"
                        onClick={() => saveProfile()}
                      >Save</Button>
                    </FormGroup>
                  </div>
                </Form>
              </CardBody>
            </Card>
          </Col>
        </Row>
      </Container>
      <NotificationSystem ref={notificationSystem} />
    </>
  );
}

export default Profile;
