import React, { useState, useEffect, useRef } from "react";
import {
  Card,
  CardHeader,
  CardFooter,
  Pagination,
  PaginationItem,
  PaginationLink,
  Table,
  Container,
  Row,
  Col,
} from "reactstrap";
import List from "list.js";
import { useDispatch, useSelector } from 'react-redux';
import { getCandidates } from 'store/zoom/actions';
import Candidate from 'components/candidates/candidate';
import NotificationSystem from 'react-notification-system';

function Candidates() {
  const candidateListRef = useRef(null);
  const notificationSystem  = useRef(null);
  const dispatch = useDispatch();
  const [pagination, setPagination] = useState(false);
  const zoomUser = useSelector(state => state.zoomData.zoomUser);
  // const zoomUser = { account_id: 'jFbi8sjpSYmTKxT4NaVdKw' }

  const candidates = useSelector(state => state.zoomData.candidates);

  useEffect(() => {
    if (zoomUser) {
      dispatch(getCandidates(zoomUser.account_id));
    } else {
      const notification = notificationSystem.current;
      
      notification.addNotification({
        title: 'Alert',
        message: 'Please login with zoom!',
        level: 'info',
      });
    }
    new List(candidateListRef.current, {
      valueNames: ["name", "email", "meeting_name", "last_active"],
      listClass: "list",
    });

  }, [])

  return (
    <>
      <Container className="mt--0" fluid>
        <h1 className="main-page-title">Candidates</h1>
        <Row>
          <Col xl="12">
            <Row>
              <div className="col" ref={candidateListRef}>
                <Card>
                  <CardHeader className="border-0">
                    <h3 className="mb-0 text-uppercase">candidates</h3>
                  </CardHeader>
                  <Table
                    className="align-items-center table-flush custom-tabel potential-condidtes-table"
                    responsive
                  >
                    <thead className="thead-light">
                      <tr>
                        <th className="sort" data-sort="name" scope="col">
                          Name
                        </th>
                        <th className="sort" data-sort="email" scope="col">
                          Email
                        </th>
                        <th className="sort" data-sort="meeting_name" scope="col">
                          Meeting Name
                        </th>
                        <th
                          className="sort"
                          data-sort="last_active"
                          scope="col"
                        >
                          Last Active
                        </th>
                        <th scope="col" />
                      </tr>
                    </thead>
                    <tbody className="list">
                      {
                        candidates && candidates.length > 0 &&
                        candidates.map((candidate, i) => {
                          return (
                            <Candidate key={i} candidate={candidate} />
                          )
                        })
                      }
                    </tbody>
                  </Table>
                  {
                    pagination &&
                    <CardFooter className="py-4">
                      <nav aria-label="...">
                        <Pagination
                          className="pagination justify-content-center mb-0"
                          listClassName="justify-content-end mb-0"
                        >
                          <PaginationItem className="disabled">
                            <PaginationLink
                              href="#pablo"
                              onClick={e => e.preventDefault()}
                              tabIndex="-1"
                            >
                              <i className="fas fa-angle-left" />
                              <span className="sr-only">Previous</span>
                            </PaginationLink>
                          </PaginationItem>
                          <PaginationItem className="active">
                            <PaginationLink
                              href="#pablo"
                              onClick={e => e.preventDefault()}
                            >
                              1
                        </PaginationLink>
                          </PaginationItem>
                          <PaginationItem>
                            <PaginationLink
                              href="#pablo"
                              onClick={e => e.preventDefault()}
                            >
                              2 <span className="sr-only">(current)</span>
                            </PaginationLink>
                          </PaginationItem>
                          <PaginationItem>
                            <PaginationLink
                              href="#pablo"
                              onClick={e => e.preventDefault()}
                            >
                              3
                        </PaginationLink>
                          </PaginationItem>
                          <PaginationItem>
                            <PaginationLink
                              href="#pablo"
                              onClick={e => e.preventDefault()}
                            >
                              <i className="fas fa-angle-right" />
                              <span className="sr-only">Next</span>
                            </PaginationLink>
                          </PaginationItem>
                        </Pagination>
                      </nav>
                    </CardFooter>
                  }
                </Card>

              </div>
            </Row>
          </Col>
        </Row>
      </Container>
      <NotificationSystem ref={notificationSystem} />
    </>
  );
}
export default Candidates;
