import React, { useEffect } from 'react';
import { Link } from 'react-router-dom';
import moment from 'moment';
import classNames from 'classnames';
import { useSelector, useDispatch } from 'react-redux';
import { changeRecordingBookmark, getParticipants } from 'store/zoom/actions';
import Participants from 'components/participants';

export default function FeaturedRecording({ meeting }) {
    const dispatch = useDispatch();
    const bookmarkClass = classNames('fas fa-bookmark', meeting.bookmarked ? 'text-info' : 'text-light');

    const token = useSelector(state => state.users.token);
    const zoomAccessToken = useSelector(state => state.zoomData.zoomAccessToken);

    useEffect(() => {
        dispatch(getParticipants(token, zoomAccessToken, meeting.uuid));
    }, [])

    const handleBookmark = () => {
        dispatch(changeRecordingBookmark(token, meeting.uuid, !meeting.bookmarked));
    }

    return (
        <tr>
            <td>
                <Link to={{ pathname: '/admin/recording', state: meeting }} className="topic" >
                    {meeting.topic}
                </Link>
            </td>
            <td className="starttime">{moment(meeting.start_time).format('h:mm A, MMM D YYYY')}</td>
            <td className="duration">{meeting.lens && `${Math.floor(meeting.lens / 3600)} : ${Math.floor(meeting.lens % 3600 / 60)} : ${Math.round(meeting.lens % 3600 % 60)}`}</td>
            <td className="timezone">{meeting.timezone}</td>
            <td>
                {
                    meeting.participants && meeting.participants.participants.length > 0 &&
                    <Participants participants={meeting.participants.participants} />
                }
            </td>
            <td className="text-right">
                <i className={bookmarkClass} onClick={handleBookmark}></i>
            </td>
        </tr>
    )
}
