import * as types from './types'
import { apiUrl } from '../../config'

export const register = ({name, email, password}) => {
  return (dispatch) => {
    fetch(apiUrl.REGISTER, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json'
      },
      body: JSON.stringify({
        email,
        password,
        username: name
      })
    })
      .then(response => response.json())
      .then(json => {
        if (!json.error) {
          dispatch(registerSuccess(json));
        } else {
          dispatch(signinError(json.errorMessage));
        }
      })
      .catch(error => {
        dispatch(signinError(error.errorMessage))
      })
  }
}

export const signin = ({email, password}) => {
  return (dispatch) => {

    fetch(apiUrl.SIGNIN, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json'
      },
      body: JSON.stringify({
        "email": email,
        "password": password,
      })
    })
      .then(response => response.json())
      .then(json => {
        if (!json.error) {
          dispatch(signinSuccess(json))
        } else {
          dispatch(signinError(json.errorMessage));
        }
      })
      .catch((error) => {
        dispatch(signinError(error.errorMessage));
      });
  }
}

export const updateProfile = (token, user) => {
  return (dispatch) => {

    fetch(apiUrl.UPDATE_PROFILE, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json',
        'Authorization': "Bearer " + token,
      },
      body: JSON.stringify(user)
    })
      .then(response => response.json())
      .then(json => {
        if (!json.error) {
          dispatch(updateProfileSuccess(json.user))
          dispatch(userMessage('Updated profile successfully'));
        } else {
          dispatch(updateProfileError(json.errorMessage));
        }
      })
      .catch((error) => {
        console.log(error)
        dispatch(updateProfileError(error.errorMessage));
      });
  }
}

export const paymentSuccess = (token, userId) => {
  return (dispatch) => {

    fetch(apiUrl.UPDATE_PAYMENT_STATUS, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json',
        'Authorization': "Bearer " + token,
      },
      body: JSON.stringify({
        userId
      })
    })
      .then(response => response.json())
      .then(json => {
        console.log(json);
        if (!json.error) {
          dispatch(updatePaymentStatusSuccess(json.payment.paid_status, json.payment.usage_amount));
        } else {
          dispatch(updatePaymentStatusFailed(json.errorMessage))
        }
      })
      .catch((error) => {
        console.log(error);
      });
  }
}

export const clearSigninError = () => {
  return {
    type: types.CLEAR_SIGNIN_ERROR
  }
}

export const signout = () => {
  return (dispatch) => {
    dispatch(singoutSuccess())
  }
}


const signinSuccess = data => ({
  type: types.SIGNIN_SUCCESS,
  data
})

const signinError = error => ({
  type: types.SIGNIN_ERROR,
  error
})

const registerSuccess = data => ({
  type: types.REGISTER_SUCCESS,
  data
})

const singoutSuccess = () => ({
  type: types.SIGNOUT_SUCCESS,
})

const updateProfileSuccess = (user) => ({
  type: types.UPDATE_PROFILE_SUCCESS,
  user
})

const updateProfileError = error => ({
  type: types.UPDATE_PROFILE_ERROR,
  error
})

export const clearUserError = () => ({
  type: types.CLEAR_USER_ERROR,
})

export const userMessage = message => ({
  type: types.USER_MESSAGE,
  message
})

export const clearUserMessage = () => ({
  type: types.CLEAR_USER_MESSAGE,
})

const updatePaymentStatusSuccess = (lastMonthPaidStatus) => ({
  type: types.UPDATE_PAYMENT_STATUS_SUCCESS,
  lastMonthPaidStatus
})

const updatePaymentStatusFailed = (error) => ({
  type: types.UPDATE_PAYMENT_STATUS_FAILED,
  error
})