import React, { useState, useEffect } from 'react';
import { useSelector } from 'react-redux';
import { Link } from 'react-router-dom';
import moment from 'moment'
import { apiUrl, sdkConfig } from 'config';

const meetingConfig = {
    apiKey: sdkConfig.API_KEY,
    apiSecret: sdkConfig.API_SECRET,
    meetingNumber: "",
    passWord: "",
    userName: "",
    userEmail: "",
    leaveUrl: sdkConfig.LEAVE_URL,
    role: 1
}

export default function MeetingItem({ meeting }) {
    const [state, setState] = useState({ meetingConfig, doRecord: false });

    const token = useSelector(state => state.users.token);
    const zoomAccessToken = useSelector(state => state.zoomData.zoomAccessToken);
    const zoomUser = useSelector(state => state.zoomData.zoomUser);

    useEffect(() => {
        fetch(apiUrl.GET_MEETING, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                'Accept': 'application/json',
                'Authorization': "Bearer " + token,
            },
            body: JSON.stringify({
                meetingId: meeting.id,
                zoomAccessToken
            })
        })
            .then(response => response.json())
            .then(data => {
                if (!data.error) {
                    if (zoomUser) {
                        setState({
                            ...state, meetingConfig: {
                                ...state.meetingConfig,
                                meetingNumber: data.meeting.id,
                                passWord: data.meeting.password,
                                userName: zoomUser.first_name + ' ' + zoomUser.last_name,
                                userEmail: zoomUser.email,
                            }
                        })
                    }
                }
            })
            .catch(error => {
                console.log(error)
                return false
            })

    }, []);

    const handleChange = (e) => {
        setState({ ...state, doRecord: !state.doRecord })
    }

    const currentTimeStamp = new Date().getTime();
    const meetingTimeStamp = new Date(meeting.start_time).getTime();

    return (
        <tr>
            <td>
                <a href={meeting.join_url} target="_blank">
                    {meeting.topic}
                </a>
            </td>
            <td>{moment(meeting.start_time).format('h:mm A, MMM D YYYY')}</td>
            <td>{currentTimeStamp > meetingTimeStamp ? 'Past' : 'Upcoming'}</td>
            <td>{`${meeting.duration} mins`}</td>
            <td>{meeting.timezone}</td>
            <td>{moment(meeting.created_at).format('h:mm A, MMM D YYYY')}</td>
            <td>
                <Link className="btn btn-primary btn-sm" to={{
                    pathname: "/ZoomMeeting",
                    state: state
                }}>Start Meeting</Link>
            </td>
            <td>
                <label className="custom-toggle custom-toggle-info mr-1">
                    <input defaultChecked={state.doRecord} onChange={handleChange} type="checkbox" />
                    <span
                        className="custom-toggle-slider rounded-circle"
                        data-label-off="No"
                        data-label-on="Yes"
                    />
                </label>
            </td>
        </tr>
    )
}
