import React, { useState, useRef, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import NotificationSystem from 'react-notification-system';
import { updateTranscript } from 'store/aws/actions';

function CommentEdit({ hidden, userId, meetingUUID, toggleCommentEdit, commentToEdit, transcripts }) {
  const [comment, setComment] = useState('');
  const dispatch = useDispatch();
  const notificationSystem = useRef(null);
  const token = useSelector(state => state.users.token);
 
  const { comments } = transcripts;

  useEffect(() => {
    if (commentToEdit.id !== 0) {
      const commentRIndex = comments.findIndex(cm => cm.id === commentToEdit.id);
      setComment(comments[commentRIndex].commentText)
    }
  }, [commentToEdit])

  const handleCommentSubmit = (e) => {
    e.preventDefault();

    const commentRIndex = comments.findIndex(cm => cm.id === commentToEdit.id);
    let updatedComments = JSON.parse(JSON.stringify(comments));

    updatedComments[commentRIndex].commentText = comment;

    dispatch(updateTranscript(token, userId, meetingUUID, {...transcripts, comments: updatedComments}))

    toggleCommentEdit();
    reset()
  }

  const handleCommentChange = (e) => {
    setComment(e.target.value);
  }

  const reset = () => { setComment(''); }

  return (
    <>
      <div hidden={hidden} className="comment-box-wrapper custom-card">
        <form
          className="comment-box"
          onSubmit={handleCommentSubmit}
        >
          <label htmlFor="commentEdit" className="visuallyhidden">
            Edit your comment
          </label>
          <textarea
            id="commentEdit"
            className="comment-box__text-area"
            placeholder="Edit your comment"
            onChange={handleCommentChange}
            value={comment}
            rows="6"
          >
          </textarea>
          <button type="submit" className="btn btn-outline-primary btn-block">
            Save
          </button>
        </form>
      </div>
      <NotificationSystem ref={notificationSystem} />
    </>
  );
}

export default CommentEdit;
