import * as types from './types'
import { SIGNOUT_SUCCESS } from '../users/types'

const initialState = {
  zoomAccessToken: '',
  zoomRefreshToken: '',
  zoomUser: null,
  zoomMeetings: null,
  uploadNotify: '',
  zoomRecordings: null,
  totalRecordingData: null,
  monthlyRecordingData: [],
  candidates: [],
  recordings: [],
  offCandidates: 0
}

const zoomData = (state = initialState, action) => {
  switch (action.type) {
    //REGISTER
    case types.GET_ACCESS_TOKEN_SUCCESS:
      return { ...state, zoomAccessToken: action.zoomData.zoomAccessToken, zoomRefreshToken: action.zoomData.zoomRefreshToken, zoomUser: action.zoomData.zoomUser, zoomMeetings: action.zoomData.zoomMeetings, zoomRecordings: action.zoomData.zoomRecordings }

    case types.REFRESH_ZOOM_ACCESS_TOKEN_SUCCESS:
      return {...state, zoomAccessToken: action.data.access_token, zoomRefreshToken: action.data.refresh_token}
      
    case types.GET_ZOOM_USERINFO_SUCCESS:
      return { ...state, zoomUser: action.zoomUser }

    case types.GET_ZOOM_USER_MEETINGS_SUCCESS:
      return { ...state, zoomMeetings: action.zoomMeetings }

    case types.UPLOAD_RECORDING_SUCCESS:
      return { ...state, uploadNotify: "success" }

    case types.UPLOAD_RECORDING_ERROR:
      return { ...state, uploadNotify: "faild" }

    case types.UPLOAD_RECORDING_NOTI_CLEAR:
      return { ...state, uploadNotify: "" }

    case types.GET_PARTICIPANTS_SUCCESS:
      const tempState = { ...state };
      const recordingItem = tempState.zoomRecordings.meetings.find(recording => {
        return recording.uuid === action.uuid;
      });
      recordingItem.participants = action.data;
      return { ...tempState }

    case types.GET_TOTAL_RECORDING_DATA_SUCCESS:
      return { ...state, totalRecordingData: action.data }

    case types.GET_MONTHLY_RECORDING_DATA_SUCCESS:
      let temp = []
      temp = action.data.map(ct => { return ct.recordingsOfMonth })
      return { ...state, monthlyRecordingData: temp }

    case types.GET_CANDIDATES_SUCCESS:
      return { ...state, candidates: action.data }

    case types.CHANGE_CANDIDATE_BOOKMARK_SUCCESS:
      let tempCandidates = [...state.candidates];

      const candidateIndex = tempCandidates.findIndex(candidate => {
        return candidate.email === action.data.email;
      });

      tempCandidates[candidateIndex].bookmarked = action.data.bookmarked;

      return { ...state, candidates: tempCandidates }

    case types.CHANGE_RECORDING_BOOKMARK_SUCCESS:
      let tempRecordings = [...state.recordings];

      const recoridngIndex = tempRecordings.findIndex(recording => {
        return recording.meeting_uuid === action.data.meeting_uuid;
      });

      tempRecordings[recoridngIndex].bookmarked = action.data.bookmarked;

      return { ...state, recordings: tempRecordings }

    case types.GET_RECORDINGS_SUCCESS:
      return { ...state, recordings: action.recordings }

    case types.GET_TOTAL_CANDATES_COUNT_SUCCESS:
      return { ...state, offCandidates: action.data.offCandidates }

    case SIGNOUT_SUCCESS:
      return initialState

    default:
      return state
  }
}

export default zoomData

